  .app-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    padding: 0rem 14rem;
  }
  
  .pokemon-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  .all-container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
  }
  
  .thumb-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 2rem 0.4rem;
    margin: 1.036em;
    border: 2px solid rgba(0,0,0,1);
    border-radius: 0.2rem;
    min-width: 160px;
    text-align: center;
    box-shadow: 0 3px 15px rgba(0,0,0,1);
    cursor: pointer;
    transition: 0.2s ease-in-out;
  }

  .thumb-container:hover {
    -webkit-transform:scale(1.1);
            transform:scale(1.1);
    box-shadow: 0 3px 17px rgba(0,0,0,1);
  }
  
  h3 {
    margin-bottom: 0;
    font-size: 20px;
    font-weight: 700;
  }
  
  .thumb-container .number {
    border-radius: 1rem;
    padding: 0.6rem 0rem;
    font-size: 20px;
    font-weight: 500;
  }
  
  .thumb-container img {
    width: 120px;
    height: 120px;
  }
  
  .detail-wrapper{
    display: flex;
    flex-direction: column;
    width: 100%;
    text-transform: capitalize;
    font-size: 17px;
    font-weight: 600;
  }
  
  .load-more {
    border-radius: 2px;
    border: 1px solid #c6c6c6;
    color: rgb(46, 46, 46);
    padding: 0.5rem 1.5rem;
    min-width: 50%;
    margin-top: 1rem;
    margin-bottom: 2rem;
    box-shadow: 0 3px 15px rgba(0,0,0,1);
    cursor: pointer;
    font-size: 18px;
    font-weight: 700;
    font-family: var(--paragraph-font);
  }

  .load-more:hover {
    box-shadow: 0 3px 15px rgba(0,0,0,1);
    background-color: rgba(0,0,0,1);
    color: #fff;
    border: 1px solid rgba(0,0,0,0.3);
  }

  h1 {
    font-size: 40px;
    text-shadow: 2px 3px 4px rgb(0 0 0 / 45%);
  }
.info-container {
    width: 100%;
    margin: 0 auto;
    padding: 0 25px;
    max-width: 1230px;
    margin-top: 3rem;
    margin-bottom: 40px;
}

.evo-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    font-weight: 500;
}

.evo-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-transform: capitalize;
}

.evo-img {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    height: 100px;
    width: 100px;
    cursor: pointer;
}

.display {
    display: inline-block;
    max-width: 100%;
    overflow: hidden;
    position: relative;
    box-sizing: border-box;
    margin: 0px;
}

.box-sizing {
    box-sizing: border-box;
    display: block;
    max-width: 100%;
}

.display img {
    box-sizing: border-box;
    padding: 0px;
    border: none;
    margin: auto;
    display: block;
    width: 260px;
    height: 260px;
}


.evo-img img {
    inset: 0px;
    box-sizing: border-box;
    padding: 0px;
    border: none;
    margin: auto;
    width: 80px;
    height: 80px;
    min-width: 100%;
    max-width: 100%;
    min-height: 100%;
    max-height: 100%;
}

.cards-container {
    margin-top: 40px;
    display: grid;
    grid-template-rows: 420px 1fr;
    grid-template-columns: unset;
    grid-gap: 50px;
    align-items: center;
    justify-items: center;
    height: -webkit-max-content;
    height: max-content;
    width: 100%;
}

.card-container {
    width: 100%;
    height: 100%;
    background: #fff;
    border-radius: 20px;
    box-shadow: 0 5px 25px 1px rgb(0 0 0 / 100%);
    padding: 10px;
}

.card {
    width: 100%;
    height: 100%;
    border-radius: 15px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.details-wrapper {
    width: 100%;
}

@media screen and (min-width: 768px) {
    .cards-container {
        grid-template-rows: unset;
        grid-template-columns: 320px 1fr;
    }
}

.details {
    margin: 20px 0;
    height: -webkit-max-content;
    height: max-content;
    padding: 0 20px;
}

.details p {
    margin: 0;
    padding: 0;
}

.card-wrapper {
    position: relative;
    width: 100%;
}

.bio-training-content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 20px;
}

.bio-text {
    margin: 0;
    padding: 0;
    font-size: 14px;
    font-family: var(--paragraph-font);
    display: block;
}

/* @media screen and (min-width: 768px) {
    .bio-training {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-gap: 20px;
    }
} */

.flex-wrapper {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.flex-wrapper p {
    font-size: 14px;
    font-weight: 500;
}

.flavor-text {
    word-wrap: break-word;
    margin-bottom: 10px;
    font-family: var(--paragraph-font);
    font-size: 14px;
    font-weight: 500;
}

.flavor-text span {
    text-transform: capitalize;
}

.stats-container {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
}

.stats {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    font-weight: 500;
}

.title {
    font-weight: 700;
    font-size: 17px;
    padding: 0;
    font-family: var(--paragraph-font);
}

.back-btn {
    padding: 5px 10px;
    border-radius: 5px;
    cursor: pointer;
    border: 1px solid black;
    color: black;
    outline: none;
    background: none;
    margin: 0;
    font-size: 14px;
    font-family: var(--paragraph-font);
    font-weight: 700;
}

.back-btn:hover {
    background: black;
    color: white;
}

.id-number {
    font-size: 40px;
    font-weight: 700;
    opacity: 0.5;
    margin:  0;
    margin-top: 5px;
    margin-bottom: 5px;
}

.type {
    font-size: 20px;
    font-weight: 700;
    text-transform: capitalize;
    opacity: 0.5;
    margin: 0;
    margin-top: 5px;
}

.name {
    font-size: 20px;
    font-weight: 700;
    text-transform: capitalize;
    opacity: 0.8;
    margin: 0;
    margin-bottom: 5px;
    
}


.abilities {
    text-transform: capitalize;
}

.bottom-section {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.poke__nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1rem;
    background: black;
    padding: 10px 0;
    box-shadow: 0 0 10px 5px rgb(0 0 0 / 31%)
}

.poke__nav-links {
    flex: 1 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 0 25px;
    margin: 0 15.9%;
}

.poke__nav-links_container {
    display: flex;
    flex-direction: row;
}

.poke__nav-links_container p {
    font-family: var(--paragraph-font);
    font-size: 24px;
    font-weight: 700;
    line-height: 32px;
    margin: 0 1rem;
    cursor: pointer;
}

.poke__nav-links_container a {
    text-decoration: none;
    color: #fff;
    transition: 0.1s ease-in-out;
}

.poke__nav-links_container a:hover {
    color: crimson;
}

.poke__nav-links_container img {
    height: 70px;
    width: 80px;
    position: absolute;
    top: -10px;
    left: 450px;
}
